@import './resets/reset.css';
@import '../containers/npc-store/_npc-store';

@mixin entity($color) {
  background: $color;
  border: 1px solid darken($color, 17%); }

$background-color: #2D2D2D;

$floor: #d6d1d1;

$enemy: #8ce114;
$neutral: green;
$weapon: #f3f70c;
$shield: #999;
$potion: #ff0000;
$player: #fc8d8d;
$exit: #00b9f1;
$boss: #c447e0;
$npc: #eac086;

$gold: #cfb53b;
$silver: #CCC;
$copper: #b87333;

$level1: #78d376;
$level2: #75b3d1;
$level3: #abaf33;
$level4: #a733af;

body {
  background-color: $background-color;
  font-family: Jura, sans-serif;
  color: #ddd; }

.container {
  margin-left: 1em;
  margin-right: 1em;
  margin-bottom: 1em; }

.strip {
  display: flex;
  justify-content: center;
  margin-top: 1em;
  margin-bottom: 1em;
  padding: 0.8em 1.2em;
  font-size: 1.2em;
  background: rgba(0,0,0,0.2); }

.strip_fullscreen {
  display: flex;
  position: fixed;
  justify-content: center;
  margin-top: 1em;
  margin-bottom: 1em;
  padding: 0.8em 1.2em;
  font-size: 1.2em;
  background: rgba(0,0,0,1);
  z-index: 2; }

.tips {
  display: flex;
  justify-content: center;
  margin-top: 1em;
  margin-bottom: 1em;
  padding: 0.8em 1.2em;
  font-size: 1.2em;
  background: rgba(0,0,0,0.2); }


h1 {
  font-weight: 700;
  font-size: 1.7em;
  font-family: 'Titillium Web'; }

#app {
  display: flex;
  justify-content: center; }

#app_fullscreen {
  display: flex;
  justify-content: left; }

.title {
  transition: color 3s; }

.title-0, .title-1, {
  color: $level1; }

.title-2, .title-transit-2 {
  color: $level2; }

.title-3, .title-transit-3 {
  color: $level3; }

.title-4, .title-transit-4 {
  color: $level4; }

.row {
  display: flex;
  justify-content: center; }

.cell {
  height: 1em;
  width: 1em;
  border: 1px solid $background-color; }

.cell_fullscreen {
  height: 2em;
  width: 2em;
  border: 1px solid $background-color; }

.back-1 {
  background: $level1; }

.back-2 {
  background: $level2; }

.back-3 {
  background: $level3; }

.back-4 {
  background: $level4; }

.back-transit-2 {
  background: $level2;
  transition: background-color 3s; }

.back-transit-3 {
  background: $level3;
  transition: background-color 3s; }

.back-transit-4 {
  background: $level4;
  transition: background-color 3s; }

.back-death {
  background: #000;
  transition: background-color 6s; }

.back-victory {
  background: #fff;
  transition: background-color 6s; }

.floor {
  background: $floor;
  border: 1px solid lighten($floor, 2%); }

.player {
  @include entity($player); }

.enemy {
  @include entity($enemy); }

.neutral {
  @include entity($neutral); }

.potion {
  @include entity($potion); }

.weapon {
  @include entity($weapon); }

.shield {
  @include entity($shield); }

.exit {
  @include entity($exit); }

.boss {
  @include entity($boss); }

.npc {
  @include entity($npc); }

.gold {
  @include entity($gold); }

.silver {
  @include entity($silver); }

.copper {
  @include entity($copper); }


.sidebar {
  display: flex;
  flex-direction: column;
  margin-left: 1em;
  margin-right: 1em;
  width: 20em;
  font-size: 0.9em;
  color: #ddd; }

.sidebar_fullscreen {
  display: flex;
  flex-direction: column;
  margin-left: 1em;
  margin-right: 1em;
  width: 20em;
  font-size: 0.9em;
  color: #ddd;
  position: absolute;
  z-index: 1;
  background: black;
  position: fixed;
  top: 40px;
  right: 40px; }

.npcStore {
  display: flex;
  flex-direction: column; }

.npcStoreButton {
  display: block;
  padding: 10px;
  margin: 10px;
  border-radius: 4px;
  pointer-events: all;
  cursor: pointer;
  background: #333;
  color: white;
  border: none; }

.grid-wrapper {
  border: 1px solid rgba(0,0,0,0.2); }

.panel {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 1em;
  padding: 0.8em 1.2em;
  background: rgba(0,0,0,0.2); }

.panel_fullscreen {
  display: flex;
  flex-direction: column;
  margin-bottom: 1em;
  padding: 0.8em 1.2em;
  background: rgba(0,0,0,0.2); }

.score-item {
  display: flex;
  margin: 0.6em 0;
  min-width: 40%; }

.score-label {
  display: flex;
  flex-direction: column;
  justify-content: flex-end; }

.setting-label {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  cursor: pointer; }

.icon {
  margin-right: 0.5em;
  border: 0; }

.triangle {
  width: 0;
  height: 0;
  border-left: 0.5em solid transparent;
  border-right: 0.5em solid transparent;
  border-bottom: 1em solid red; }

.restart-btn {
  height: 1em;
  width: 1em;
  margin-right: 0.5em;
  border: 1px solid #ddd;
  border-radius: 50%;
  &:hover {
    cursor: pointer; }
  &:active {
    background: red; } }

input {
  position: absolute;
  left: -9999px; }

label {
  cursor: pointer; }

label::before {
  display: inline-block;
  content: '';
  height: 1em;
  width: 1em;
  border: 1px solid #ddd;
  margin-right: 0.5em;
  vertical-align: bottom; }

input:checked + label::before {
  background: red; }

.messages {
  ul {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    margin: 0; }
  li {
    margin-bottom: 0.5em;
    list-style-type: none;
    font-size: 0.9em;
    opacity: 0.3;
    &:last-child {
      margin-bottom: 0;
      opacity: 1.0;
      color: #78d376; } } }


